import navBar from "../../components/nav-bar.vue";

export default {
	data() {
		return {
			meTop:{
				osrc:"../../static/jks.jpg",
				otitle:"金克斯",
				qianm:"金克斯的含义就是金克斯"
			},
			qiandao:"签到",
			animationData: {},
			trueFalse: true
		};
	},
	onLoad() {
		var _me = this;
		var new_date = new Date();
		if(_me.meTop.osrc == ""){
			_me.meTop.osrc = "../../static/toux.jpg"
		}
		if(_me.meTop.otitle == ""){
			_me.meTop.osrc = "请设置昵称"
		}
		if(_me.meTop.oqianm == ""){
			_me.meTop.osrc = "请设置个人签名"
		}
		
		
		uni.getStorage({
			key:'qiandao',
			success(e) {
				if(e.data[0] == true && e.data[1] == new_date.getDate()){
					_me.qiandao = "已签到";
					_me.trueFalse = false;
				}
			}
		});
	},
	computed:{
		oziliao1:function(){
			return this.$store.state.nicheng.oname
		},
		oziliao2:function(){
			return this.$store.state.ziliao.ojianjie
		},
		oziliao3:function(){
			return this.$store.state.ziliao.osrc
		}
	},
	methods: {
		ziliao(){
			uni.navigateTo({
				url: '../my/ziliao'
			});
		},
		qianDao(){
			var _me = this;
			var new_date2 = new Date();
			if(_me.trueFalse){
				uni.setStorage({
					key: 'qiandao',
					data: [true,new_date2.getDate()],
					success: function () {
						_me.trueFalse = false;
						_me.qiandao = "已签到";

						
						var animation = uni.createAnimation({
							duration: 1000,
							timingFunction: 'ease',
						})
									
						_me.animation = animation;
						_me.animation.translateY(-20).opacity(1).step({
							duration: 400
						});
						_me.animationData = _me.animation.export();
						// 还原动画
						setTimeout(function(){
							_me.animation.translateY(0).opacity(0).step({
								duration: 0
							});
							_me.animationData = _me.animation;
						}.bind(_me),1500);

					}
				});
			}
		}
	},
	components:{
		navBar
	}
}
